<template>
  <div class="home">
    <div ref='Area'></div>
    <transition name="el-zoom-in-center">
      <div class="sign-all"  v-show="showSign">
        <div class="sign"></div>
        <div class="sign-x"></div>
      </div>
    </transition>
    <el-collapse-transition>
      <div class="card" v-show="showCard" v-on:click="click">
        <p class="content">“我们已经在一起”</p>
        <p class="content">“{{ day }} 天”</p>
        <p class="content">“{{ hour }} 小时”</p>
        <p class="content">“{{ minute }} 分”</p>
        <p class="content">“{{ second }} 秒”</p>
        <p class="content">“{{ millisecond }} 豪秒”</p>
      </div>
    </el-collapse-transition>
  </div>
</template>

<script>
import {onDeactivated, onMounted, ref, watch} from "vue";
import * as THREE from 'three'
import BIRDS from 'vanta/src/vanta.birds'
import {ElNotification} from "element-plus";

export default {
  name: 'HomeView',
  components: {
  },
  setup: function () {
    const showCard = ref(false);
    const showSign = ref(false);
    // 定时器
    const timer = ref(0);
    // 毫秒
    const millisecond = ref(0);
    // 秒
    const second = ref(0);
    // 分
    const minute = ref(0);
    // 时
    const hour = ref(0);
    // 天
    const day = ref(0);
    // 次数
    const count = ref(1);
    // 使用ref引用挂载区域
    const Area = ref(null);
    const clientHeight = ref(document.body.clientHeight);
    const clientWidth = ref(document.body.clientWidth);
    watch(
        ()=>[clientHeight.value,clientWidth.value],
        ([heightVal,heightPreVal],[widthVal,widthPreVal])=>{
          //监听多个源用数组传入
          console.log("countVal,countPreVal",heightVal+"--"+heightPreVal,widthVal+"--"+widthPreVal)
          // const vantaCanvas = document.getElementsByClassName("vanta-canvas");
          // vantaCanvas[0].style.height = (heightVal);
          // vantaCanvas[0].style.width = (widthVal);
          // vantaCanvas[0].setAttribute("width",widthVal.toString());
          // vantaCanvas[0].setAttribute("height",heightVal.toString());
        }
    )
    //组件挂载时的生命周期执行的方法
    onMounted(()=>{
      // eslint-disable-next-line no-empty
      if (clientWidth.value > 500){
        clientWidth.value = 500;
      }
      window.onresize = function(){
        clientHeight.value = document.body.clientHeight;
        clientWidth.value = document.body.clientWidth;
      }

      timer.value = window.setInterval(function getTime() {
        //getTime() 返回从 1970 年 1 月 1 日至今的毫秒数。
        //1秒=1000毫秒  3600=1小时*1分钟   24=24小时
        // 获取中间时间戳
        const number = new Date().getTime()-new Date("2022/09/22 00:28:12").getTime();
        // 毫秒
        millisecond.value = number;
        // 秒
        second.value = parseInt((number / 1000).toString());
        // 分
        minute.value = parseInt((number / (1000 * 60)).toString());
        // 时
        hour.value = parseInt((number / (1000 * 60 * 60)).toString());
        // 天
        day.value = parseInt((number / (1000 * 60 * 60 * 24)).toString());
      }, 1);

      window.setTimeout(function transition() {
        showCard.value = true;
      }, 1000);

      window.setTimeout(function transition() {
        showSign.value = true;
      }, 1500);

      BIRDS({
        el:Area.value,
        THREE:THREE,
        // 如果需要改变样式，要写在这里
        // 因为这里vantaEffect是没有setOptions这个方法的
        mouseControls: true,
        touchControls: true,
        minHeight: clientHeight.value-5,
        minWidth: clientWidth.value-5,
        scale: 1.00,
        scaleMobile: 1.00,
        backgroundColor: 0xffffff,
        color1: 0x9a2626,
        colorMode: "lerp",
        birdSize: 1.50,
        wingSpan: 20.00,
        speedLimit: 6.00,
        separation: 50.00,
        alignment: 39.00,
        cohesion: 29.00,
        quantity: 4.00,
        backgroundAlpha: 0.00
      })
    })
    const format = (percentage) => (percentage === 100 ? 'Full' : `${percentage}%`)
    // 离开当前组件的生命周期执行的方法
    onDeactivated(()=>{
      window.clearInterval(timer.value);
    })

    const click = () => {
      // eslint-disable-next-line no-empty
      switch (count.value)
      {
        case 1:
          count.value = count.value+1;
          ElNotification({
            title: '消息',
            message: '今天是我们在一起的第'+day.value+'天耶！！！',
            type: 'success',
          });
          break;
        case 2:
          count.value = count.value+1;
          ElNotification({
            title: '消息',
            message: '快看加起来一共有'+hour.value+'个小时哎，厉害吧！',
            type: 'success',
          });
          break;
        case 3:
          count.value = count.value+1;
          ElNotification({
            title: '消息',
            message: '我们一定会很久很久的对吧！！！！',
            type: 'success',
          });
          break;
        case 4:
          count.value = count.value+1;
          ElNotification({
            title: '消息',
            message: '就像毫秒一样，没有尽头对吧！！',
            type: 'success',
          });
          break;
        case 10:
          count.value = count.value+1;
          ElNotification({
            title: '消息',
            message: '没想到吧还有，爱你哦！！',
            type: 'success',
          });
          break;
        default:
          count.value = count.value+1;
          ElNotification({
            title: '消息',
            message: '我爱你向星雨，不要再点了后面真没了嘿嘿！！',
            type: 'success',
          });
      }

    }
    // 暴露 state 到模板
    return {
      second,
      minute,
      hour,
      day,
      millisecond,
      showCard,
      showSign,
      Area,
      format,
      click
    }
  }
}
</script>

<style scoped>
/*1.相对路径: "./assets/logo_blue.png"*/
/*2.没有前缀的路径 "assets/logo_blue.png" 被webpack解析为 相对路径*/
/*3.带~的路径  "~@/assets/theme/logo_blue.png" 被webpack解析为 require(src/assets/theme/logo_blue.png) 动态引入  @在webpack 被resolve.alias配置下等价于/src*/
/*4.相对根目录的路径 "/assets/logo_blue.png" webpack不解析*/
.content {
  color: rgba(6, 62, 124, 0.8);
  font-weight: bold;
  margin-top: 10px;
  margin-bottom: 10px;
}
.sign-all{
  width: 100%;
  position: absolute;
  top: 80px;
  left: 0;
  right: 0;
  margin: auto;
}
.sign {
  display: inline-block;
  height: 80px;
  width: 170px;
  background: url("~@/assets/sign.gif");
  background-size: 100% 100%;
  border-radius: 15px;
  transform: rotate(345deg);
}

.sign-x{
  display: inline-block;
  height: 80px;
  width: 170px;
  background: url("~@/assets/signx.gif");
  background-size: 100% 100%;
  border-radius: 15px;
  margin-right: 25px;
  margin-bottom: 30px;
  transform: rotate(30deg);
}

.card{
  position: absolute;
  bottom: 60px;
  left: 0;
  right: 0;
  margin: auto;
  width: 80%;
  display: inline-block;
  text-align: center;
  border-radius: 20px;
  /* 设置渐变 */
  background: linear-gradient(
      to right bottom,
      rgba(255,255,255,.6),
      rgba(255,255,255,.3),
      rgba(255,255,255,.2)
  );
  /* 重点：使元素背景模糊化 */
  backdrop-filter: blur(11px);
  /* 设置上高光和左高光，使其看起来更加逼真 */
  border-top: 1px solid rgba(255,255,255,.8);
  border-left: 1px solid rgba(255,255,255,.8);
}

/* 在大于或等于 1200 像素的屏幕上 */
@media screen and (min-width: 500px) {
  .home {
    display: inline-block;
    text-align: center;
    height: 100%;
    width: 500px;
    background: url("~@/assets/background.jpg");
    background-size: 100% 100%;
    position: relative;
  }

}

/* 在 600 像素或更小的屏幕上，将背景色设置为橄榄色 */
@media screen and (max-width: 500px) {
  .home {
    display: inline-block;
    text-align: center;
    height: 100%;
    width: 100%;
    background: url("~@/assets/background.jpg");
    background-size: 100% 100%;
    position: relative;
  }

}
</style>
